.App { padding: 5px;}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 20px;
}
.header img {
    max-width: 80px;
}
.header h2 {
    margin-top: 20px;
    text-align: center;
    color: #1c6f71;
    font-size: 1.5rem;
}

/*  */
.plans h1 {
    font-family: 'Open Sans Condensed', sans-serif;
    text-align: center;
    font-size: 2.4rem;
    font-weight: bold;
    color: #0d4d4e;
    margin-bottom: 20px;
}
.plans p {
    font-size: 1.2rem;
}

/*  */
.plan-info {
    display: flex;
    flex-direction: column;
    align-items: center;

    box-shadow: 0px 0px 10px #ccc;
    margin: 0 10px;
    padding: 15px;
}
.plan-info h3 {
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Open Sans Condensed', sans-serif;
}
.plan-info p.planPrice {
    margin: 20px 0;
}
.plan-info p.planPrice span {
    font-size: .7rem;
}
.plan-info button {
    border: none;
    border-radius: 4px;

    width: 100%;
    padding: 20px 10px;
    background: #38c4ac;
    color: #fff;
    font-weight: bold;
    font-size: 1rem;
}

/*  */
.plan-items {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.plan-items p {
    font-size: 1.2rem;
    font-weight: bold;
}
.plan-items ul {
    display: flex;
    list-style: none;
    width: 100%;
    margin: 20px 0;;
}
.plan-items ul li{
    width: 25%;
    padding: 15px 0;
    font-size: 1.2rem;
    text-align: center;
    border: solid 1px #ddd;
    border-right: none;
    background: transparent;
}
.plan-items ul li:last-child {
    border-right: solid 1px #ddd;
    border-top-right-radius: 5px;
}
.plan-items ul li:first-child {
    border-bottom-left-radius: 5px;
}
.plan-items .planActive {
    background: #38c4ac;
    color: #fff;
}

/*  */
.how-it-works {
    margin: 30px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.how-it-works-item {
    padding: 10px;
    margin: 30px 0;
    width: 100%;
}
.how-it-works-item ul {
    list-style: none;
}
.how-it-works-item .emoji {
    font-size: 5rem;
    text-shadow: 0px 0px 20px rgba(0,0,0,.3);
}
.how-it-works-item h3 {
    font-size: 1.5rem;
    text-transform: uppercase;
    margin-bottom: 10px;
}
.how-it-works-item p {
    font-size: 1.2rem;;
}

/*  */
.mealStyle {
    margin: 100px 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}